<template>
  <breadcrumb-list :items="breadcrumbs"></breadcrumb-list>

  <data-table :titles="titles" :collection="surveys">
    <template #filters>
      <div class="py-3">
        <div class="grid grid-cols-1">
          <div class="block">
            <label for="textSearch">{{ $t("attributes.name") }}</label>
            <div class="w-full mt-1.5">
              <input
                v-model="surveys.filters.search"
                id="textSearch"
                type="text"
                :placeholder="$t('placeholder.search')"
                class="w-full rounded-lg border border-slate-300 bg-transparent px-3 py-2 placeholder:text-slate-400/70 form-input hover:border-slate-400 focus:border-primary dark:border-navy-450 dark:hover:border-navy-400 dark:focus:border-accent"
              />
            </div>
          </div>
        </div>
      </div>
    </template>

    <template #body>
      <tr v-for="survey in surveys" :key="survey.id">
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ survey.id }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ survey.name }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ $t(`models.survey.states.${survey.state}`) }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          <router-link
            :to="{
              name: 'app.workspace.edit',
              params: { id: survey.group.workspace_id },
            }"
            class="text-info hover:text-primary"
          >
            {{ survey.group.workspace_name }}
          </router-link>
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ survey.group.name }}
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          <router-link
            :to="{
              name: 'app.template.edit',
              params: { id: survey.template.id },
            }"
            class="text-info hover:text-primary"
          >
            {{ survey.template.name }}
          </router-link>
        </td>
        <td class="font-medium text-slate-700 dark:text-navy-100">
          {{ survey.surveyAtLocal }}
        </td>
        <td>
          <div class="flex space-x-2">
            <!--          <router-link-->
            <!--            :to="{ name: 'admin.hospital.edit', params: { id: hospital.id } }"-->
            <!--            :title="$t('labels.edit')"-->
            <!--            class="h-10 w-10 rounded-full p-0 font-medium text-white btn bg-primary hover:bg-primary-focus focus:bg-primary-focus active:bg-primary-focus/90 dark:bg-accent dark:hover:bg-accent-focus dark:focus:bg-accent-focus dark:active:bg-accent/90"-->
            <!--          >-->
            <!--            <v-icon name="pencil-square" size="5"></v-icon>-->
            <!--          </router-link>-->

            <v-button
              color="info"
              icon
              :size="10"
              :title="$t('labels.fill_survey_for_patient')"
              @click="fillSurveyModal(survey)"
            >
              <v-icon name="clipboard-document-check" size="5"></v-icon>
            </v-button>

            <v-button
              v-if="survey.isDeletable"
              color="error"
              icon
              :size="10"
              :title="$t('labels.delete')"
              @click="deleteModal(survey)"
            >
              <v-icon name="archive-delete" size="5"></v-icon>
            </v-button>
          </div>
        </td>
      </tr>
    </template>
  </data-table>

  <v-modal ref="modal">
    <template #title>{{ $t("modal.titles.delete") }}</template>
    <template #body>{{ $t("modal.messages.delete") }}</template>
    <template #buttons>
      <v-button color="error" @click="deleteData">
        {{ $t("labels.delete") }}
      </v-button>
    </template>
  </v-modal>

  <v-modal ref="surveyFillModal">
    <template #title>{{ $t("labels.fill_survey_for_patient") }}</template>
    <template #body>
      <vue-select
        :options="patients.get()"
        :get-option-label="(patient) => patient.label"
        :reduce="(patient) => patient.id"
        v-model="patientId"
        :placeholder="$t('placeholder.select_patient')"
      >
      </vue-select>
    </template>
    <template #buttons>
      <v-button
        color="primary"
        @click="goToFill"
        :disabled="!survey || !patientId"
      >
        {{ $t("labels.submit") }}
      </v-button>
    </template>
  </v-modal>
</template>

<script>
import SurveyCollection from "@/collections/SurveyCollection";
import ModelRequest from "@/request/ModelRequest";
import DataTable from "@/components/DataTable/DataTable.vue";
import PatientCollection from "@/collections/PatientCollection";
import SurveyItem from "@/models/SurveyItem";

export default {
  name: "AppSurveyList",
  components: { DataTable },
  data() {
    return {
      breadcrumbs: [{ title: this.$t("titles.surveys"), active: true }],
      titles: [
        { column: "id", title: "#", sortable: true },
        { column: "name", title: this.$t("attributes.name"), sortable: true },
        { column: "state", title: this.$t("attributes.state"), sortable: true },
        {
          title: this.$t("attributes.workspace"),
        },
        {
          column: "group_id",
          title: this.$t("attributes.group"),
          sortable: true,
        },
        {
          column: "template_id",
          title: this.$t("attributes.template"),
          sortable: true,
        },
        {
          column: "survey_at",
          title: this.$t("attributes.survey_at"),
          sortable: true,
        },
        { title: this.$t("labels.actions") },
      ],
      surveys: new SurveyCollection(),
      deleteSelect: null,
      patients: new PatientCollection(),
      survey: null,
      patientId: null,
    };
  },
  created() {
    this.surveys.fetchWithFilters();
  },
  methods: {
    deleteModal(model) {
      this.deleteSelect = model;
      this.$refs.modal.toggle();
    },
    deleteData() {
      const request = new ModelRequest(this.deleteSelect);

      request.delete().then(() => {
        this.$refs.modal.toggle();

        this.surveys.fetchWithFilters();
      });
    },
    fillSurveyModal(survey) {
      this.survey = survey;
      this.patientId = null;

      this.patients.fetch(`/survey/${survey.id}/patients`);

      this.$refs.surveyFillModal.toggle();
    },
    goToFill() {
      if (this.survey && this.patientId) {
        const surveyItem = new SurveyItem();

        surveyItem
          .fetchFind(`/survey-item/find/${this.survey.id}/${this.patientId}`)
          .then(() => {
            if (surveyItem.id) {
              this.$refs.surveyFillModal.toggle();

              this.$router.push({
                name: "app.survey.fill",
                params: { id: surveyItem.id },
              });
            }
          });
      }
    },
  },
};
</script>